import React from "react";

const products = [
  { id: 1, name: "Wheat", price: 1200, desc: "High quality wheat from Punjab.", img: "https://images.unsplash.com/photo-1465101046530-73398c7f28ca?auto=format&fit=crop&w=400&q=80" },
  { id: 2, name: "Rice", price: 1500, desc: "Premium Basmati rice.", img: "https://images.unsplash.com/photo-1519125323398-675f0ddb6308?auto=format&fit=crop&w=400&q=80" },
  { id: 3, name: "Maize", price: 1000, desc: "Fresh maize from local farms.", img: "https://images.unsplash.com/photo-1506744038136-46273834b3fb?auto=format&fit=crop&w=400&q=80" },
];

export default function Marketplace() {
  return (
    <div className="py-8 px-4 max-w-5xl mx-auto">
      <h2 className="text-3xl font-bold text-green-700 mb-6">Marketplace</h2>
      <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
        {products.map((p) => (
          <div key={p.id} className="bg-white rounded-2xl shadow-lg p-6 flex flex-col items-center">
            <img src={p.img} alt={p.name} className="w-32 h-32 object-cover rounded-full mb-4" />
            <h3 className="text-xl font-bold text-green-700 mb-2">{p.name}</h3>
            <p className="text-gray-600 mb-2">{p.desc}</p>
            <span className="text-green-600 font-bold text-lg mb-2">{p.price}/quintal</span>
            <button className="px-4 py-2 bg-green-600 text-white rounded-full shadow hover:bg-green-700 transition">Buy Now</button>
          </div>
        ))}
      </div>
    </div>
  );
}
