import React from "react";

const schemes = [
  { id: 1, name: "PM-KISAN", desc: "Direct income support to farmers." },
  { id: 2, name: "Soil Health Card", desc: "Improving soil fertility and productivity." },
  { id: 3, name: "Crop Insurance", desc: "Protection against crop loss." },
];

export default function Schemes() {
  return (
    <div className="py-8 px-4 max-w-5xl mx-auto">
      <h2 className="text-3xl font-bold text-green-700 mb-6">Schemes</h2>
      <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
        {schemes.map((s) => (
          <div key={s.id} className="bg-white rounded-2xl shadow-lg p-6 flex flex-col items-center">
            <h3 className="text-xl font-bold text-green-700 mb-2">{s.name}</h3>
            <p className="text-gray-600 mb-2">{s.desc}</p>
            <button className="px-4 py-2 bg-green-600 text-white rounded-full shadow hover:bg-green-700 transition">Learn More</button>
          </div>
        ))}
      </div>
    </div>
  );
}
