import React, { useState } from "react";

export default function Login() {
  const [msg, setMsg] = useState("");
  return (
    <div className="py-8 px-4 max-w-md mx-auto">
      <h2 className="text-3xl font-bold text-green-700 mb-6">Login</h2>
      <form
        className="bg-white rounded-2xl shadow-lg p-6 flex flex-col gap-4"
        onSubmit={e => {
          e.preventDefault();
          setMsg("Login successful!");
        }}
      >
        <input type="text" placeholder="Username" className="w-full p-3 border rounded focus:outline-none focus:ring-2 focus:ring-green-400 transition" />
        <input type="password" placeholder="Password" className="w-full p-3 border rounded focus:outline-none focus:ring-2 focus:ring-green-400 transition" />
        <button type="submit" className="bg-green-600 text-white px-6 py-3 rounded-full shadow-lg hover:bg-green-700 transition-all duration-300 transform hover:scale-105">Login</button>
      </form>
      {msg && <div className="mt-6 text-center text-green-700 font-bold">{msg}</div>}
    </div>
  );
}
