import React, { useState } from "react";

export default function DiseaseDetection() {
  const [result, setResult] = useState(null);
  return (
    <div className="py-8 px-4 max-w-2xl mx-auto">
      <h2 className="text-3xl font-bold text-green-700 mb-6">Disease Detection</h2>
      <form
        className="bg-white rounded-2xl shadow-lg p-6 flex flex-col items-center"
        onSubmit={e => {
          e.preventDefault();
          setResult("No disease detected. Crop is healthy!");
        }}
      >
        <label className="block mb-2 text-gray-700 font-semibold">Upload Crop Image</label>
        <input type="file" accept="image/*" className="w-full mb-4 px-4 py-2 border rounded-lg" />
        <button type="submit" className="w-full px-4 py-2 bg-green-600 text-white rounded-full shadow hover:bg-green-700 transition">Detect Disease</button>
      </form>
      {result && <div className="mt-8 text-center text-green-700 font-bold">{result}</div>}
    </div>
  );
}
